import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-events-details',
  templateUrl: './events-details.page.html',
  styleUrls: ['./events-details.page.scss'],
})
export class EventsDetailsPage implements OnInit {
  currentColor:any;
  image:any;
  start_date_month:any;
  start_date_day:any;
  name:any;
  start_date:any;
  place:any;
  city:any;
  state:any;
  country:any;
  descripstion:any;
  constructor(public router: Router) {
    this.currentColor = 'light';
    
    const data = this.router.getCurrentNavigation().extras.state.data;
this.image=data.image;
this.start_date_month=data.start_date_month;
this.start_date_day=data.start_date_day;
this.name=data.name;
this.start_date=data.start_date;
this.place=data.place.name;
this.city=data.place.location.city;
this.state=data.place.location.state;
this.country=data.place.location.country;
this.descripstion=data.description;
   }

  ngOnInit() {

    
  }

}

